/* Space */    
$spaceLarge: 24px;
$spaceMedium: 16px;
$spaceSmall: 12px;
$spaceXLarge: 32px;
$spaceXSmall: 8px;
$spaceXXLarge: 40px;
$spaceXXXLarge: 52px;
$spaceXXXSmall: 4px;
$spaceXXXXSmall: 2px;
$spaceNormal: 14px;
$spaceXXSmall: 6px;

/* Font size */
$FontSizeHeadingTitle1: 24px;
$FontSizeHeadingTitle2: 16px;
$FontSizeHeadingTitle3: 16px;  
$FontSizeHeadingTitle4: 14px;
$FontSizeFormLabel: 16px;
$FontSizeTextNormal: 14px;
$FontSizeTextSmall: 12px;
$FontSizeButtonSmall: 14px;

/* Grid */
$layout-2xs: calc(4px - 0.25rem);
$layout-xs: calc(8px - 0.5rem);
$layout-sm: calc(12px - 0.75rem);
$layout-md: calc(16px - 1rem);
$layout-lg: calc(24px - 1.5rem);
$layout-xl: calc(32px - 2rem);
$layout-2xl: calc(40px - 2.5rem);
$layout-3xl: calc(48px - 3rem);
$layout-4xl: calc(64px - 4rem);

/* mixin */

@mixin mobile {
    @media (min-width: 640px) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: 1537px) {
        @content;
    }
}